  min-height: 67vh; }
  @media only screen and (max-width: 768px) {
    .cltvo-reset {
      padding: 12vh 30px; } }
  @media only screen and (max-width: 480px) {
    .cltvo-reset {
      padding: 12vh 15px; } }
  .cltvo-reset button.woocommerce-Button.button {
    margin: 30px 0 15px; }

/* === M y    a c c o u n t === */
/* N a v i g a t i o n    m y    a c c o u n t */
.woocommerce-LostPassword.lost_password a {
  text-decoration: underline; }

@media only screen and (max-width: 768px) {
  .cltvo-account-nav {
    padding: 10vh 0px 2vh; } }

.cltvo-account-nav .woocommerce-MyAccount-navigation ul {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center; }
  @media only screen and (max-width: 768px) {
    .cltvo-account-nav .woocommerce-MyAccount-navigation ul {
      flex-direction: column;
      height: auto; } }
  @media only screen and (max-width: 768px) {
    .cltvo-account-nav .woocommerce-MyAccount-navigation ul li {
      margin-bottom: 10px; } }

/* C o n t e n t    a c c o u n t */
.cltvo-account-content {
  padding: 10vh 90px;
  /* T í t u l o s */
  /* B o t ó n   d e   v e r  c o m p r a s */ }
  @media only screen and (max-width: 768px) {
    .cltvo-account-content {
      padding: 10vh 30px; } }
  @media only screen and (max-width: 480px) {
    .cltvo-account-content {
      padding: 10vh 15px; } }
  .cltvo-account-content h2.woocommerce-column__title {
    margin: 30px 0 15px; }
  .cltvo-account-content button.woocommerce-Button.button {
    margin-top: 20px;
    border: 2px solid #000 !important; }
  .cltvo-account-content .woocommerce-MyAccount-content strong {
    font-weight: normal; }
  .cltvo-account-content .woocommerce-MyAccount-content a {
    color: #ababab; }
    .cltvo-account-content .woocommerce-MyAccount-content a:hover {
      color: #d8d8d8; }

.cltvo-account-nav .woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--customer-logout a {
  padding: 8px 35px !important; }

/* O r d e r */
.cltvo-order .woocommerce-notice, .cltvo-order .woocommerce-order-details__title, .cltvo-order .woocommerce-column__title {
  margin-bottom: 15px; }

.cltvo-order .woocommerce-order-details {
  margin-top: 20px; }

/* M y    a c c o u n t */
.woocommerce-account .woocommerce-MyAccount-navigation {
  width: 100%;
  float: none; }

.woocommerce-account .woocommerce-MyAccount-content {
  width: 100%; }

.woocommerce-error, .woocommerce-info, .woocommerce-message {
  font-family: "Circular Std", sans-serif;
  color: #A5A5A5;
  width: 100% !important;
  max-width: 1201px !important;
  margin: 0 auto !important;
  padding: 20px 72px !important;
  font-size: 22px;
  background-color: white !important;
  border: none !important;
  border-radius: 0 0 5px 5px;
  box-shadow: 0 4px 10px -4px #ACACAC;
  height: auto;
  display: block;
  line-height: 40px;
  align-items: center;
  justify-content: space-between;
  position: fixed !important;
  left: 50%;
  top: 0%;
  transform: translate(-50%, 0%);
  z-index: 2; }
  .woocommerce-error:before, .woocommerce-error:after, .woocommerce-info:before, .woocommerce-info:after, .woocommerce-message:before, .woocommerce-message:after {
    display: none !important; }

.woocommerce-info {
  color: #797a7c !important; }

.woocommerce-error {
  color: #C6969F !important; }

.woocommerce-message {
  color: #BDC37F !important;
  border-top: none !important;
  position: relative; }
  .woocommerce-message a {
    float: right; }

ul.woocommerce-error.woocommerce-alert.hide {
  display: none; }

.woocommerce-alert .closeAlert {
  cursor: pointer; }
  .woocommerce-alert .closeAlert:before {
    width: 13px;
    height: 13px;
    right: 15px;
    top: 4px;
    position: absolute;
    content: url(../images/close.svg); }

.woocommerce-error.woocommerce-alert {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start; }
  .woocommerce-error.woocommerce-alert strong {
    padding: 0px 40px 0px 20px;
    font-weight: bold; }
    .woocommerce-error.woocommerce-alert strong:first-child {
      padding: 0px 40px 0px 0px; }

.home__container {
  max-width: 1240px;
  height: auto;
  margin: 0 auto; }
  .home__container-image {
    width: 100%;
    height: 100%; }
